import React, { useState, useRef, useEffect, useContext } from 'react';
import posed, { PoseGroup } from 'react-pose';
import { Message, MessageContext } from '../../contexts/messages';
import './AnimatedList.scss';

interface Props {
    status: string;
    messages: Array<Message>;
   
}

const AnimatedItem = posed.span({
    exit: {
        rotateX: '90deg',
        opacity: 0,
    },
    enter: {
        rotateX: '0deg',
        opacity: 1,
    },
    unselected: {
        position: 'static',
        width: '100%',
        flip: true,
        left: 0,
    },
    selected: {
        position: 'fixed',
        flip: true,
        bottom: 0,
        left: 4,
    },
});
const formatDate = (date: Date): string => {
    const m = date.getMinutes();
    const h = date.getHours();
    return `${h}:${m < 10 ? `0${m}` : m} Hs`;
};
const AnimatedList: React.FC<Props> = ({ messages, status }) => {
    const [selected, setSelected] = useState<Array<string>>([]);
    const { name, deleteMessage } = useContext(MessageContext);
    const listRef = useRef(null);
    const onSelectMessage = (id: string) => {
        if (selected.includes(id)) {
            deleteMessage(id);
            const newSelected = selected.filter((selected: string) => id !== selected);
            setSelected(newSelected);
            localStorage.setItem('selectedImages', JSON.stringify(newSelected));
        } else {
            const newSelected = [...selected, id];
            setSelected(newSelected);
            localStorage.setItem('selectedImages', JSON.stringify(newSelected));
        }
    };
    const isAnImageURL = (body: string = ''): boolean => {
        return /(https?:\/\/.*\.(?:png|jpg))/.test(body);
    };
    useEffect(() => {
        if (messages.length && listRef) {
            listRef.current.scrollIntoView({ behavior: 'smooth' });
        }
    }, [messages, listRef]);
    return (
        <div className="list">
            <PoseGroup>
                {messages.map((message: Message) => {
                    const { id, body, date, user } = message;
                    const isSelected = selected.includes(id);
                    return (
                        <AnimatedItem
                            pose={isSelected && isAnImageURL(body) ? 'selected' : 'unselected'}
                            className={`message ${isSelected ? 'selected' : ''} ${
                                name === user ? 'own' : ''
                            }`}
                            key={`message${id}`}>
                            <div className={`content `} onClick={() => onSelectMessage(id)}>
                                <p className="header">
                                    <span className="user">{user}</span>
                                    <span className="date">{formatDate(new Date(date))}</span>
                                </p>
                                {isAnImageURL(body) ? (
                                    <img
                                        src={body}
                                        alt="This message is an URL "
                                        className="isAnImage"
                                    />
                                ) : (
                                    <p className="body">{body}</p>
                                )}
                            </div>
                        </AnimatedItem>
                    );
                })}
            </PoseGroup>
            {status === 'fetching' && <p>fetching</p>}
            <span className="dummy" ref={listRef} />
        </div>
    );
};

export default AnimatedList;
