import React, { useState, useEffect } from 'react';
import app from 'firebase/app';
import 'firebase/auth';
import 'firebase/firestore';
import config from '../configs/firebase';

export interface Message {
    id: string;
    user: string;
    body: string;
    date: string;
}
interface MessageContext {
    name: string;
    status: string;
    messages: Array<Message>;
    push: (message: string) => void;
    deleteMessage: (id: string) => void;
}
const getRandonName = (): string => {
    const names = ['Player 1', 'Player 2', 'Player 3', 'Player 4', 'Player 5', 'Player 6'];
    const randomIndex = Math.floor(Math.random() * names.length);

    return names[randomIndex];
};

const voidContext: MessageContext = {
    name: '',
    status: '',
    messages: [],
    push: (message: string) => console.warn('Firebase not initialized'),
    deleteMessage: (id: string) => console.warn('Firebase not initialized'),
};
const MessageContext = React.createContext<MessageContext>(voidContext);

const MessageProvider: React.FC = ({ children }) => {
    const [status, setStatus] = useState('initalizing');
    const [name, setName] = useState(getRandonName());
    const [messages, setMessages] = useState<Array<Message>>([]);

    // tokenize
    const isCommand = async (body: string): Promise<boolean> => {
        if (/(?:\\name)([ a-z0-9]+)/.test(body)) {
            const newName = /(?:\\name)([ a-z0-9]+)/.exec(body)[1];
            if (newName) setName(newName);
            return true;
        }
        return false;
    };
    const push = async (body: string) => {
        const newMessage = { user: name, body, date: new Date().getTime() };
        if (await isCommand(body)) return;

        let collectionRef = app.firestore().collection('real-time-chat');
        try {
            await collectionRef.add(newMessage);
        } catch (e) {
            setStatus(`Can't push a new message`);
        }
    };
    const deleteMessage = (id: string) => {
        let collectionRef = app.firestore().collection('real-time-chat');

        collectionRef
            .doc(id)
            .delete()
            .catch(e => {
                setStatus(`Can't push a new message`);
            });
    };
    // load last theme
    useEffect(() => {
        setStatus('fetching');
        let unsubscribe = () => console.warn('fake unsuscribe');
        try {
            app.initializeApp(config);
            const query = app
                .firestore()
                .collection('real-time-chat')
                .orderBy('date');
            unsubscribe = query.onSnapshot(
                querySnapshot => {
                    const newMessages = [];
                    querySnapshot.forEach(doc => {
                        newMessages.push({ ...doc.data(), id: doc.id });
                    });
                    setMessages(newMessages);
                    setStatus('fetched');
                },
                err => {
                    setStatus(err.message);
                    console.error(`Encountered error: ${err}`);
                },
            );
        } catch (err) {
            console.error(err);
        }
        // Remove this listener.
        return unsubscribe;
    }, [setStatus, setMessages]);
    const messageHandler = {
        name,
        status,
        messages,
        push,
        deleteMessage,
    };
    return <MessageContext.Provider value={messageHandler}>{children}</MessageContext.Provider>;
};

export { MessageContext, MessageProvider };
